/* Start:: Form Steps ------------- */
.steps{
	padding-left:0
}
.steps:after{
	content:" ";
	clear:both;
	display:table
}
.steps li{
	list-style-type:none;
	float:left;
	font-size:1rem;
	position:relative;
	text-align:center;
	color:#8795AA
}
.steps li span{
	max-width:7rem;
	display:inline-block
}
@media (min-width: 801px){
	.steps li span{
			max-width:9rem
	}
}
.steps li:before{
	width:2rem;
	height:2rem;
	content:"";
	border:2px solid #B6BFCC;
	display:block;
	text-align:center;
	margin:0 auto 0.5rem auto;
	border-radius:50%;
	position:relative;
	z-index:2;
	background-color:#fff
}
.steps li:after{
	width:100%;
	height:2px;
	content:'';
	position:absolute;
	background-color:#B6BFCC;
	top:1rem;
	left:-50%;
	z-index:0
}
.steps li:first-child:after{
	content:none
}
.steps li.active{
	color:#007bff;
	font-weight:500
}
.steps li.active:before{
	box-shadow:0px 0.125rem 0.25rem rgba(0,0,0,0.08),0px 0.25rem 1.5rem rgba(0,0,0,0.08);
	background-color:#007bff;
	border:solid 10px white
}
.steps li.done{
	color:#00112C;
	font-weight:normal
}
.steps li.done:before{
	background:#007bff url(//cdn2.dan.com/assets/svg-icons/check-mark-white-49016bc8abbf7d575ab9972f9a1e1cfc77e5493b9b110f71cae5d5f100564601.svg) 50% 50% no-repeat;
	border:none;
	box-shadow:0px 0.125rem 0.25rem rgba(0,0,0,0.08),0px 0.25rem 1.5rem rgba(0,0,0,0.08)
}
.steps li.done+li:after{
	background-color:#007bff
}
.steps li.spacer:before{
	visibility:hidden
}
.rounded-1-5{
	border-radius: 0.5rem;
}
.elevate{
	box-shadow: 0px 0.125rem 0.25rem rgb(0 0 0 / 8%), 0px 0.25rem 1.5rem rgb(0 0 0 / 8%);
	border-color: transparent;
}
.p-1-5{
